import React, { Component } from 'react'
export default class BangDan extends Component {
    constructor(){
        super();
        this.state = {
            data:[ {music_id:'千里随风',music_name:'你若成风',music_author:'感觉不错'},
            {music_id:'梦回',music_name:'当你老了',music_author:'还可以'},
            {music_id:'十年十月',music_name:'你一生的故事',music_author:'滑稽一笑'},
            {music_id:'西游',music_name:'谢你过我平庸的一生',music_author:'池年的歌真好听'},
            {music_id:'红楼',music_name:'老街',music_author:'李荣浩第一'},
            {music_id:'三国',music_name:'如果当时',music_author:'才华横溢'},
            {music_id:'水浒',music_name:'未见青山老',music_author:'。。。'},
            {music_id:'奥特玛',music_name:'如约而至',music_author:'你已经因为言论过激被踢出房间'},
            {music_id:'152',music_name:'风雨彩虹铿锵玫瑰',music_author:'田震万岁'},
            {music_id:'007',music_name:'像风一样',music_author:'薛之谦真强'}],
        };
    }
    delete(e){
        var arr = [...this.state.data];
        arr.splice(e.target.id,1);
        this.setState({
            data:arr,
        })
    }
    render() {
        return (
            <div className='main'>
                <div className="gebang">
                <div id="page-wrapper">
                    <div id="page-inner">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="panel panel-default">
                                    <div className="panel-body">
                                        <div className="table-responsive">
                                            <table  id="dataTables-example">
                                                <thead>
                                                    <tr>
                                                        <th style={{textAlign:'center',border: '1px grey solid',backgroundColor: '#CCE8EB',width:'72px',height:'53px'}}>评论人</th>
                                                        <th style={{textAlign:'center',border: '1px grey  solid',backgroundColor: '#CCE8EB',width:'460px',height:'53px'}}>评论歌曲</th>
                                                        <th style={{textAlign:'center',border: '1px grey solid',backgroundColor: '#CCE8EB',width:'230px',height:'53px'}}>评论内容</th>
                                                        <th style={{textAlign:'center',border: '1px grey  solid',backgroundColor: '#CCE8EB',width:'230px',height:'53px'}}>删除评论</th>
                                                           
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    {
                                                        this.state.data.map(
                                                            (item,index)=>(
                                                                <tr key={index}>
                                                                    <td style={{textAlign:'center',border: '1px grey  solid',width:'72px',height:'53px'}}>{item.music_id}</td>
                                                                    <td style={{textAlign:'center',border: '1px grey  solid',width:'460px',height:'53px'}}>{item.music_name}</td>
                                                                    <td style={{textAlign:'center',border: '1px grey  solid',width:'230px',height:'53px'}}>{item.music_author}</td>  
                                                                    <td style={{textAlign:'center',border: '1px grey  solid',width:'230px',height:'53px'}}><button style={{width:'100%',height:'100%',textAlign:'center',color:'red'}} onClick={(e)=>this.delete(e)} id={index}>删除评论</button></td>                           
                                                                    
                                                                </tr>
                                                            )
                                                        )
                                                    }
                                                </tbody>
                                            </table>
                                        </div>                       
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
        
            </div>
              </div>
            
        )
    }
}
