import React, { Component } from 'react';
import {Link} from 'react-router-dom';
export default class GeCi extends Component {
    constructor(){
        super();
        this.state = {
            data:[
                {music_id:'千里随风',music_name:'2019-12-01',music_author:'感觉不错'},
            {music_id:'梦回',music_name:'2019-12-03',music_author:'还可以'},
            {music_id:'十年十月',music_name:'2019-12-01',music_author:'滑稽一笑'},
            {music_id:'西游',music_name:'2019-12-01',music_author:'池年的歌真好听'},
            {music_id:'红楼',music_name:'2019-12-04',music_author:'李荣浩第一'},
            {music_id:'三国',music_name:'2019-12-01',music_author:'才华横溢'},
            {music_id:'水浒',music_name:'2019-12-05',music_author:'。。。'},
            {music_id:'奥特玛',music_name:'2019-12-07',music_author:'你已经因为言论过激被踢出房间'},
            {music_id:'152',music_name:'2019-12-08',music_author:'田震万岁'},
            {music_id:'007',music_name:'2019-12-11',music_author:'薛之谦真强'}
            ],

        };

    }
    delete(e){
        var arr = [...this.state.data];
        arr.splice(e.target.id,1);
        this.setState({
            data:arr,
        })
    }
    render() {
        return (
            <div className='main'>
            <div id="page-wrapper">
            <div id="page-inner">
                <div className="row">
                    <div className="col-md-12">
                        <div className="panel panel-default">
                            <div className="panel-body">
                                <div className="table-responsive">
                                    <table  id="dataTables-example">
                                        <thead>
                                            <tr>
                                                <th style={{textAlign:'center',border: '1px grey solid',width:'82px',height:'53px', backgroundColor: '#CCE8EB'}}>发布用户</th>
                                                <th style={{textAlign:'center',border: '1px grey solid',width:'230px',height:'53px', backgroundColor: '#CCE8EB'}}>发布时间</th>
                                                <th style={{textAlign:'center',border: '1px grey solid',width:'450px',height:'53px', backgroundColor: '#CCE8EB'}}>发布内容</th>
                                                <th style={{textAlign:'center',border: '1px grey solid',width:'230px',height:'53px', backgroundColor: '#CCE8EB'}}>删除动态</th>
                                                   
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {
                                                this.state.data.map(
                                                    (item,index)=>(
                                                        <tr key={index}>
                                                            <td style={{textAlign:'center',border: '1px grey solid',width:'82px',height:'53px'}}>{item.music_id}</td>
                                                            <td style={{textAlign:'center',border: '1px grey solid',width:'230px',height:'53px'}}>{item.music_name}</td>
                                                            <td style={{textAlign:'center',border: '1px grey solid',width:'420px',height:'53px'}}>{item.music_author}</td>  
                                                            <td style={{textAlign:'center',border: '1px grey solid',width:'230px',height:'53px'}}><button style={{width:'100%',height:'100%',textAlign:'center',color:'red'}} onClick={(e)=>this.delete(e)} id={index}>删除动态</button></td>                           
                                                            
                                                        </tr>
                                                    )
                                                )
                                            }
                                        </tbody>
                                    </table>
                                </div>                       
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

            </div>
        )
    }
}
