import React, { Component } from 'react'
export default class Users extends Component {
    constructor(){
        super();
        this.state = {
            data:[
                {id:1,name:'千里随风',account:'17315709784'},
                {id:2,name:'梦回',account:'17315709789'},
                {id:3,name:'十年十月',account:'17315719784'},
                {id:4,name:'西游',account:'17315709369'},
                {id:5,name:'红楼',account:'17315709896'},
                {id:6,name:'三国',account:'17315709799'},
                {id:7,name:'水浒',account:'17315709701'},
                {id:8,name:'奥特玛',account:'1731570284'},
                {id:9,name:'152',account:'17315709734'},
                {id:10,name:'007',account:'17315709384'},
        ],
        id:'',
        name:'',
        account:''

        };

    }
    delete(e){
        var arr = [...this.state.data];
        arr.splice(e.target.id,1);
        this.setState({
            data:arr,
        })
    }
    changeid(e){
        this.setState({
            id:e.target.value
        })
    }
    changename(e){
        this.setState({
            name:e.target.value
        })
    }
    changeaccount(e){
        this.setState({
            account:e.target.value
        })
    }
    adduser(e){
        var arr = [...this.state.data];
       arr.push({id:this.state.id,name:this.state.name,account:this.state.account});
       this.setState({
           data:arr,
           id:'',
           name:'',
           account:''
       })
    }
    render() {
        return (
                   <div className='main'>
                            <div style={{position:'relative'}}>
                <table  id="dataTables-example">
                    <thead>
                        <tr>
                            <th style={{textAlign:'center',border: '1px grey solid',width:'157px',height:'53px',backgroundColor: '#CCE8EB'}}>用户id</th>
                            <th style={{textAlign:'center',border: '1px grey solid',width:'224px',height:'53px',backgroundColor: '#CCE8EB'}}>用户名称</th>
                            <th style={{textAlign:'center',border: '1px grey solid',width:'385px',height:'53px',backgroundColor: '#CCE8EB'}}>用户账号</th>
                            <th style={{textAlign:'center',border: '1px grey solid',width:'226px',height:'53px',backgroundColor: '#CCE8EB'}}>删除用户</th>                               
                        </tr>
                    </thead>
                    <tbody>
                        {
                            this.state.data.map(
                                (item,index)=>{
                                    return (
                                        <tr key={index}>
                                        <td style={{textAlign:'center',border: '1px grey solid',width:'157px',height:'53px'}}>{item.id}</td>
                                        <td style={{textAlign:'center',border: '1px grey solid',width:'224px',height:'53px'}}>{item.name}</td>
                                        <td style={{textAlign:'center',border: '1px grey solid',width:'385px',height:'53px'}}>{item.account}</td>
                                        <td style={{textAlign:'center',border: '1px grey solid',width:'226px',height:'53px'}}><button style={{width:'100%',height:'100%',textAlign:'center',color:'red'}} onClick={(e)=>this.delete(e)} id={index}>删除用户</button></td>                               
                                    </tr>
                                    )
                                    
                            })
                        }
                    </tbody>
                    
                </table>
                <div>
                    <label htmlFor = 'inp'>添加用户：</label>
                    <input  placeholder = '输入添加的用户id' style={{width:'150px',height:'30px',backgroudColor:'gray'}} onChange={e=>this.changeid(e)} value={this.state.id}></input>
                    <input  placeholder = '输入添加的用户名称' style={{width:'150px',height:'30px',backgroudColor:'gray'}} onChange={e=>this.changename(e)} value={this.state.name}></input>
                    <input  placeholder = '输入添加的用户账号' style={{width:'150px',height:'30px',backgroudColor:'gray'}} onChange={e=>this.changeaccount(e)} value={this.state.account}></input>
                    <button style={{width:'150px',height:'30px',backgroudColor:'gray'}} onClick={(e)=>this.adduser(e)}>提交</button>

                </div>
                </div>
              </div>
         
                
                
        )
    }
}
