import React, { Component } from 'react'
import Header from './components/Header'
import Title from './components/Title'
import GeDan from './container/GeDan';
import GeCi from './container/GeCi';
import Users from './container/Users';
import BangDan from './container/BangDan';
import AppLogin from './components/AppLogin'
import {HashRouter as Router,Route} from 'react-router-dom';

export default class App extends Component {
  render() {
      return (
          <Router>
          <div className = 'inital'>
              <Header/>
              <Route exact path='/' component={AppLogin} />
              <div className='litter'>
              <Route path='/title' component={Title} />
              <Route path='/title/gedan' component={GeDan} />
              <Route path='/title/active' component={GeCi} />
              <Route path='/title/users' component={Users} />
              <Route path='/title/remark' component={BangDan} />
              </div>
              
          </div>
        </Router>
      )
  }
}
