import React, { Component } from 'react'
export default class GeDan extends Component {
    constructor(){
        super();
        this.state = {
            data:[
                {music_id:1,music_name:'你若成风',music_author:'许嵩'},
                {music_id:2,music_name:'当你老了',music_author:'赵照'},
                {music_id:3,music_name:'你一生的故事',music_author:'以冬'},
                {music_id:4,music_name:'谢你过我平庸的一生',music_author:'池年'},
                {music_id:5,music_name:'老街',music_author:'李荣浩'},
                {music_id:6,music_name:'如果当时',music_author:'许嵩'},
                {music_id:7,music_name:'未见青山老',music_author:'以冬'},
                {music_id:8,music_name:'如约而至',music_author:'许嵩'},
                {music_id:9,music_name:'风雨彩虹铿锵玫瑰',music_author:'田震'},
                {music_id:10,music_name:'像风一样',music_author:'薛之谦'}
            ],

        };
    }
   
    delete(e){
        var arr = [...this.state.data];
        arr.splice(e.target.id,1);
        this.setState({
            data:arr,
            name:'',
            author:''
        })
    }
    changename(e){
        this.setState({
            name:e.target.value
        })
    }
    changeauthor(e){
        this.setState({
            author:e.target.value
        })
    }
    addsong(e){
       var arr = [...this.state.data];
       arr.push({music_id:arr[arr.length-1].music_id + 1,music_name:this.state.name,music_author:this.state.author});
       this.setState({
           data:arr,
           name:'',
           author:''
       })

    }
    render() {
        return (
            <div className='main'>
                          <div className="gebang">
            <div id="page-wrapper">
                <div id="page-inner">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="panel panel-default">
                                <div className="panel-body">
                                    <div className="table-responsive">
                                        <table  id="dataTables-example">
                                            <thead>
                                                <tr>
                                                    <th style={{textAlign:'center',border: '1px grey solid',width:'42px',height:'53px', backgroundColor: '#CCE8EB'}}>id</th>
                                                    <th style={{textAlign:'center',border: '1px grey solid',width:'490px',height:'53px', backgroundColor: '#CCE8EB'}}>歌曲名称</th>
                                                    <th style={{textAlign:'center',border: '1px grey solid',width:'230px',height:'53px', backgroundColor: '#CCE8EB'}}>歌曲作者</th>
                                                    <th style={{textAlign:'center',border: '1px grey solid',width:'230px',height:'53px', backgroundColor: '#CCE8EB'}}>删除歌曲</th>
                                                       
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {
                                                    this.state.data.map(
                                                        (item,index)=>(
                                                            <tr key={index}>
                                                                <td style={{textAlign:'center',border: '1px grey solid',width:'42px',height:'53px'}}>{index+1}</td>
                                                                <td style={{textAlign:'center',border: '1px grey solid',width:'490px',height:'53px'}}>{item.music_name}</td>
                                                                <td style={{textAlign:'center',border: '1px grey solid',width:'230px',height:'53px'}}>{item.music_author}</td>  
                                                                <td style={{textAlign:'center',border: '1px grey solid',width:'230px',height:'53px'}}><button style={{width:'100%',height:'100%',textAlign:'center',color:'red'}} onClick={(e)=>this.delete(e)} id={index}>删除歌曲</button></td>                           
                                                                
                                                            </tr>
                                                        )
                                                    )
                                                }
                                            </tbody>
                                        </table>
                                    </div>                       
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div>
                <label htmlFor = 'inp'>添加歌曲：</label>
                <input  placeholder = '输入添加的歌曲名称' style={{width:'150px',height:'30px',backgroudColor:'gray'}} onChange={e=>this.changename(e)} value={this.state.name}></input>
                <input  placeholder = '输入添加的歌曲作者' style={{width:'150px',height:'30px',backgroudColor:'gray'}} onChange={e=>this.changeauthor(e)} value={this.state.author}></input>
                <button style={{width:'150px',height:'30px',backgroudColor:'gray'}} onClick={(e)=>this.addsong(e)}>提交</button>
            </div>
        </div>
            </div>
        )
    }
}
